<template><section><html><head></head><body><h1>BoxGeometry、BoxOutlineGeometry</h1>
<ul>
<li><code v-pre="">vc-geometry-box</code> 组件用于加载立方体。需要作为 <code v-pre="">vc-instance-geometry</code> 的子组件使用。</li>
<li><code v-pre="">vc-geometry-outline-box</code> 组件用于加载立方体轮廓。需要作为 <code v-pre="">vc-instance-geometry</code> 的子组件使用。</li>
<li>需要作为 <code v-pre="">vc-instance-geometry</code> 的子组件使用，可以挂载到 <code v-pre="">vc-primitive</code>。</li>
</ul>
<h2>示例</h2>
<h3>加载立方体几何体</h3>
<h4>预览</h4>
<doc-preview>
  <template>
    <div class="viewer">
      <vc-viewer @ready="ready" @left_click="LEFT_CLICK">
        <vc-primitive @click="clicked" :appearance="appearance">
          <vc-instance-geometry :geometry.sync="geometry" :attributes="attributes" :model-matrix="modelMatrix">
            <vc-geometry-box ref="box" :dimensions="dimensions"></vc-geometry-box>
          </vc-instance-geometry>
        </vc-primitive>
        <vc-primitive @click="clicked" :appearance="appearance">
          <vc-instance-geometry :attributes="attributes" :model-matrix="modelMatrix2">
            <vc-geometry-outline-box ref="boxOutline" :dimensions="dimensions"></vc-geometry-outline-box>
          </vc-instance-geometry>
        </vc-primitive>
      </vc-viewer>
    </div>
  </template>
  
</doc-preview>
