          <span class="hljs-keyword">const</span> boundingSphere = <span class="hljs-title class_">BoundingSphere</span>.<span class="hljs-title function_">transform</span>(geometry.<span class="hljs-property">boundingSphere</span>, cur.<span class="hljs-property">vm</span>.<span class="hljs-property">$parent</span>.<span class="hljs-property">modelMatrix</span>)
          <span class="hljs-keyword">return</span> prev === <span class="hljs-literal">null</span> ? boundingSphere : <span class="hljs-title class_">BoundingSphere</span>.<span class="hljs-title function_">union</span>(prev, boundingSphere)
        }, <span class="hljs-literal">null</span>)
        instances[<span class="hljs-number">0</span>].<span class="hljs-property">viewer</span>.<span class="hljs-property">scene</span>.<span class="hljs-property">camera</span>.<span class="hljs-title function_">flyToBoundingSphere</span>(boundingSphereUnion)
      })
    },
    <span class="hljs-attr">methods</span>: {
