        <span class="hljs-keyword">const</span> { <span class="hljs-title class_">ColorGeometryInstanceAttribute</span>, <span class="hljs-title class_">PerInstanceColorAppearance</span>, <span class="hljs-title class_">Matrix4</span>, <span class="hljs-title class_">Cartesian3</span>, <span class="hljs-title class_">Transforms</span> } = <span class="hljs-title class_">Cesium</span>
