<h2>属性</h2>
<h3><code v-pre="">vc-geometry-box</code></h3>
<table>
<thead>
<tr>
<th>属性名</th>
<th>类型</th>
<th>默认值</th>
<th>描述</th>
</tr>
</thead>
<tbody>
<tr>
<td>dimensions</td>
<td>Object</td>
<td></td>
<td><code v-pre="">required</code> 指定 box 的长宽高。 <strong>结构：{ x: number, y: number, z: number }</strong></td>
</tr>
<tr>
<td>vertexFormat</td>
<td>Object</td>
<td></td>
<td><code v-pre="">optional</code> 指定要计算的顶点属性。</td>
</tr>
</tbody>
</table>
<hr>
<h3><code v-pre="">vc-geometry-outline-box</code></h3>
<table>
<thead>
<tr>
<th>属性名</th>
<th>类型</th>
<th>默认值</th>
<th>描述</th>
</tr>
</thead>
<tbody>
<tr>
<td>dimensions</td>
<td>Object</td>
<td></td>
<td><code v-pre="">required</code> 指定 box 的长宽高。 <strong>结构：{ x: number, y: number, z: number }</strong></td>
</tr>
</tbody>
</table>
<hr>
<p>参考官方文档：<strong><a href="https://cesium.com/docs/cesiumjs-ref-doc/BoxOutlineGeometry.html">BoxOutlineGeometry</a></strong>、<strong><a href="https://cesium.com/docs/cesiumjs-ref-doc/BoxGeometry.html">BoxGeometry</a></strong></p>
<h2>事件</h2>
<table>
<thead>
<tr>
<th>事件名</th>
<th>参数</th>
<th>描述</th>
</tr>
</thead>
<tbody>
<tr>
<td>ready</td>
<td>{Cesium, viewer, cesiumObject}</td>
<td>该组件渲染完毕时触发，返回 Cesium 类, viewer 实例，以及当前组件的 cesiumObject。</td>
</tr>
</tbody>
</table>
<hr>
</body></html></section></template>

<script>
    export default {
      data() {
        return {
          appearance: null,
          attributes: null,
          geometry: null,
          modelMatrix: null,
          modelMatrix2: null,
          dimensions: { x: 400000.0, y: 300000.0, z: 500000.0 }
        }
      },
      mounted() {
        Promise.all([this.$refs.box.createPromise, this.$refs.boxOutline.createPromise]).then((instances) => {
          console.log('All geometries are loaded.')
          const { BoundingSphere } = Cesium
          const boundingSphereUnion = instances.reduce((prev, cur) => {
            const geometry = cur.cesiumObject.constructor.createGeometry(cur.cesiumObject)
            const boundingSphere = BoundingSphere.transform(geometry.boundingSphere, cur.vm.$parent.modelMatrix)
            return prev === null ? boundingSphere : BoundingSphere.union(prev, boundingSphere)
          }, null)
          instances[0].viewer.scene.camera.flyToBoundingSphere(boundingSphereUnion)
        })
      },
      methods: {
        clicked (a) {
          console.log(a)
        },
        ready({ Cesium, viewer, cesiumObject }) {
          window.viewer = viewer
          this.viewer = viewer
          const { ColorGeometryInstanceAttribute, PerInstanceColorAppearance, Matrix4, Cartesian3, Transforms } = Cesium
          this.attributes = {
            color: new ColorGeometryInstanceAttribute(Math.random(), Math.random(), Math.random(), 0.5)
          }
          this.appearance = new PerInstanceColorAppearance({
            flat: true
          })
          this.modelMatrix = Matrix4.multiplyByTranslation(
            Transforms.eastNorthUpToFixedFrame(Cartesian3.fromDegrees(105.0, 40.0)),
            new Cartesian3(0.0, 0.0, 250000),
            new Matrix4()
          )
          this.modelMatrix2 = Matrix4.multiplyByTranslation(
            Transforms.eastNorthUpToFixedFrame(Cartesian3.fromDegrees(110.0, 40.0)),
            new Cartesian3(0.0, 0.0, 250000),
            new Matrix4()
          )
        },
        LEFT_CLICK(movement) {
          const feature = this.viewer.scene.pick(movement.position)
          console.log(feature)
        }
      }
    }
  </script>